@use "../../../../assets/styles/variables" as *;
@use "../../../../assets/styles/mixins" as *;


.advantages-section {
  background-color: $main-color;
  width: 100%;
  &__container {
    display: flex;
    justify-content: center;
    flex-direction: row;
    padding-inline: 70px;

    @include media(1400px) {
      padding-inline: 0;
    }

    @include media($breakpoints-tablet-landscape) {
      flex-wrap: wrap;
    }
  }
}